@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}

.entrada_blog {
  margin-bottom: 2rem;
}
.entrada_blog:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .entrada_blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada_blog img {
  border-radius: 1rem;
}

.entrada_texto a {
  display: block;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .entrada_texto a {
    margin-top: 0;
  }
}
.entrada_texto h4 {
  color: #000000;
  margin: 0;
}
.entrada_texto h4::after {
  content: "";
  width: 15rem;
  display: block;
  height: 0.5rem;
  background-color: #2e2344;
}
.entrada_texto p {
  color: #000000;
  font-weight: 700;
}
.entrada_texto span {
  color: #06568d;
  font-weight: bold;
}

.info-meta {
  font-weight: 700;
}
.info-meta span {
  color: #E08709;
}